<template>
    <v-toolbar app>
      <v-toolbar-title class="headline text-uppercase">
      <v-btn
        flat
        href="/"
        target="_self"
      >
       <v-flex class="font-weight-light">SUGO.EE </v-flex>
      </v-btn>
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <v-btn
        flat
        href="/#/Signin"
        target="_self"
      >
      <v-flex class="font-weight-light"> {{loginBtnLabel }} </v-flex>
      </v-btn>
    </v-toolbar>
</template>

<script>
import axios from 'axios'
export default {
    name: "MainToolbar",
    data() {
        return {
            isLogined: false,
        }
    },
    methods:{ 
        checkLoginStatus(){
          axios.get('http://54.180.37.121:8090/Status').then(res => {
					if (res.data == 'YES'){
                        this.isLogined = true;
                    }
                    console.log(res)
                    })
        }

    },
    mounted(){
        this.checkLoginStatus();
    },
    computed: {
        loginBtnLabel(){
            if (this.isLogined) {
                return 'LOGOUT'}
            else { return 'LOGIN' }
        }
    }
}
</script>